@import "../../global.scss";

.menu{
     width: 300px;
     height: 100vh;
     background-color: $mainColor;
     position: fixed;
     top:0;
     right: -300px;
     z-index:19;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     transition: all 1s ease;

     @include mobile{
          width: 100vw;
          right: -100vw;
     }

     &.active{
          right: 0;
     }

     ul{
         margin: 0;
         padding: 0;
         list-style: none;
         font-size: 30px;
         font-weight: 300;
         color: white;
         width: max-content;

          li{
               margin: auto auto;
               margin-bottom: 30px;
               width: max-content;
               a{
                   font-size: inherit;
                   color: inherit;
                   text-decoration: none;
                   transition: all 0.5s ease;
               }
               
               &:hover{
                    font-weight: 700;
               }
          }
     }
}